body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  width: 100vw;
  max-width: 480px;
  height: 100vh;
  max-height: 650px;
  overflow: hidden;
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; }

.informer {
  position: absolute;
  top: 40px;
  right: 40px;
  background: #34393c;
  border: white 0.2rem solid;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  line-height: 80px;
  text-align: center;
  color: white;
  font-size: 2rem;
  color: white;
  font-style: oblique; }
  .informer::after {
    content: "km";
    display: block;
    background: #0f1314;
    border: white 0.2rem solid;
    width: 60px;
    height: 30px;
    line-height: 30px;
    position: absolute;
    text-align: center;
    font-size: 1.2rem;
    z-index: -1;
    right: -60px;
    top: 25px; }

.startgame {
  position: absolute;
  bottom: 20%;
  left: 0;
  right: 0;
  margin: auto;
  width: 166px;
  height: 46px;
  line-height: 46px;
  background-image: url(data:image/png;base64,); }

.restartgame {
  background-image: url(../../static/media/restart.4809e548.png);
  display: block;
  background-size: 50px;
  width: 50px;
  height: 53px;
  margin: auto; }

.gameover {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 80;
  opacity: 0.8;
  background: black; }
  .gameover ul {
    list-style: none;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    color: white;
    width: 150px;
    height: 200px;
    line-height: 40px;
    margin: auto;
    text-align: center; }
  .gameover li {
    margin: 10px 0;
    font-size: 1.2rem; }
    .gameover li.title {
      font-size: 2rem; }

/* road */
.road {
  width: 100vw;
  height: 204vh;
  overflow: hidden;
  position: absolute;
  z-index: -1;
  left: 0;
  top: -100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  -webkit-animation: roadMove 4s linear infinite;
          animation: roadMove 4s linear infinite; }
  .road.running {
    -webkit-animation-play-state: running;
            animation-play-state: running; }
  .road.paused {
    -webkit-animation-play-state: paused;
            animation-play-state: paused; }

.road-col {
  margin: 0;
  padding: 0;
  height: 100%; }

.road-col-1 {
  width: 12.5vw;
  max-width: 60px;
  background: url(data:image/png;base64,) repeat-y;
  background-size: 100%;
  background-color: #a7c9cb; }

.road-col-2 {
  width: 25vw;
  max-width: 120px;
  background: url(data:image/png;base64,) repeat-y;
  background-size: 100%;
  background-color: #a7c9cb; }

.road-col-3 {
  width: 25vw;
  max-width: 120px;
  background: url(data:image/png;base64,) repeat-y center;
  background-size: 20px;
  background-color: #a7c9cb; }

.road-col-4 {
  width: 25vw;
  max-width: 120px;
  background: url(data:image/png;base64,) repeat-y;
  background-size: 100%;
  background-color: #a7c9cb; }

.road-col-5 {
  width: 12.5vw;
  max-width: 60px;
  background: url(data:image/png;base64,) repeat-y;
  background-size: 100%;
  background-color: #a7c9cb; }

@-webkit-keyframes roadMove {
  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh); } }

@keyframes roadMove {
  100% {
    -webkit-transform: translateY(100vh);
            transform: translateY(100vh); } }

/* hero */
.hero {
  background: url(https://s2.ax1x.com/2019/04/02/Ayj4ot.png) no-repeat;
  position: absolute;
  bottom: 0;
  z-index: 10;
  width: 40px;
  height: 75px;
  padding-bottom: 15px; }
  .hero.right {
    left: 316px; }
  .hero.left {
    left: 124px; }
  @media (max-width: 480px) {
    .hero.right {
      left: calc(70vw - 20px); }
    .hero.left {
      left: calc(30vw - 20px); } }
  .hero .life {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 13px;
    overflow: hidden;
    background: url(data:image/png;base64,) repeat-x;
    background-size: auto 13px; }
    .hero .life.life1 {
      width: 13px; }
    .hero .life.life2 {
      width: 26px; }
    .hero .life.life3 {
      width: 39px; }

/* traffic light */
.trafficlight {
  -webkit-animation: lightMove 4s linear;
          animation: lightMove 4s linear;
  width: 36vw;
  max-width: 172.8px;
  height: 60px;
  position: absolute;
  z-index: 20;
  top: -60px;
  left: 60px; }
  .trafficlight.red {
    background: url(../../static/media/trafficlightred.4481a323.png) no-repeat;
    background-size: 172.8px; }
  .trafficlight.green {
    background: url(../../static/media/trafficlightgreen.0bccbda3.png) no-repeat;
    background-size: 172.8px; }
  .trafficlight.yellow {
    background: url(../../static/media/trafficlightyellow.2bed1ffa.png) no-repeat;
    background-size: 172.8px; }
  .trafficlight.running {
    -webkit-animation-play-state: running;
            animation-play-state: running; }
  .trafficlight.paused {
    -webkit-animation-play-state: paused;
            animation-play-state: paused; }
  @media (max-width: 480px) {
    .trafficlight {
      left: 12.5vw;
      width: 36vw; }
      .trafficlight.red, .trafficlight.green, .trafficlight.yellow {
        background-size: 36vw; } }

@-webkit-keyframes lightMove {
  100% {
    -webkit-transform: translateY(104vh);
            transform: translateY(104vh); } }

@keyframes lightMove {
  100% {
    -webkit-transform: translateY(104vh);
            transform: translateY(104vh); } }

